import * as THREE from 'three';

/**
 *   
 * @type {BoxGeometry}
 */
const GRAVE_GEOMETRY = new THREE.BoxBufferGeometry(
    0.6,
    0.8,
    0.2
);

/**
 *   
 * @type {MeshStandardMaterial}
 */
const GRAVE_MATERIAL = new THREE.MeshStandardMaterial(
    {
        color: '#b2b6b1'
    }
);

/**
 *   
 * @type {Group}
 */
const GRAVES_GROUP = new THREE.Group();

//   
for (let i = 0; i < 50; i++) {

    const RANDOM_ANGLE = Math.random() * Math.PI * 2                //  
    const RANDOM_RADIUS = 3 + Math.random() * 6                     //  
    const X_POSITION = Math.cos(RANDOM_ANGLE) * RANDOM_RADIUS       //   x,  
    const Z_POSITION = Math.sin(RANDOM_ANGLE) * RANDOM_RADIUS       //   z,  

    //   
    const GRAVE = new THREE.Mesh(
        GRAVE_GEOMETRY,
        GRAVE_MATERIAL
    );

    //      
    GRAVE.position.set(
        X_POSITION,
        0.3,
        Z_POSITION
    );

    //         
    GRAVE.rotation.z = (Math.random() - 0.5) * 0.4;
    GRAVE.rotation.y = (Math.random() - 0.5) * 0.4;
    GRAVE.castShadow = true;

    //    
    GRAVES_GROUP.add(GRAVE);
}

export {
    GRAVES_GROUP
};